// Bootstrap
import { Container, Row, Col, Button } from 'react-bootstrap';
import Nav from "react-bootstrap/Nav";

// LOGO
import logo from '../../../assets/images/safa-dark-logo.svg';

// SCSS
import styles from './styles.module.scss';

// Icon
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faTwitter,
  faFacebookF,
  faGoogle,
  faLinkedinIn,
} from '@fortawesome/free-brands-svg-icons';


import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';


export default function Footer() {
  const { t } = useTranslation();

  const navbarItems = [
    {
      id: 1,
      name: t('home'),
      href: "/"
    },
    {
      id: 2,
      name: t('navbar.aboutUs'),
      href: "/about-us"
    },
    {
      id: 3,
      name: t('navbar.services'),
      href: "/services"
    },
    {
      id: 4,
      name: t('navbar.events'),
      href: "/events"
    },
    {
      id: 5,
      name: t('navbar.courses'),
      href: "/courses"
    },
    {
      id: 6,
      name: t('navbar.seminars'),
      href: "/webinars"
    },
    {
      id: 7,
      name: t('navbar.workShops'),
      href: "/work-shops"
    },
    {
      id: 8,
      name: t('navbar.news'),
      href: "/news"
    },
    {
      id: 9,
      name: t('navbar.research'),
      href: "/research"
    },
    {
      id: 10,
      name: t('navbar.teamWork'),
      href: "/team-work"
    },
    {
      id: 11,
      name: t('navbar.memberShip'),
      href: "/membership"
    }, {
      id: 12,
      name: t('navbar.contactUs'),
      href: "/contact-us"
    },
  ];

  return (
    <div className={styles.footer}>
      <Container fluid className=" my-5">
        <Row>
          <Col xs={12} md={4} className={`${styles.logo} mb-4 `}>
            <Link to="/" className="mb-4">
              <img src={logo} alt="Safa logo" />
            </Link>
            <p className="my-3">Safa@gmail.com</p>
            <p>+757 699-4478</p>
          </Col>
          <Col xs={12} md={4} className="mb-3">
            <h4 className="mb-4"> {t('footer.links')} </h4>
            <div className="d-flex justify-content-center justify-content-md-start">
              <Nav defaultActiveKey="/home" className="flex-column me-5">
                {
                  navbarItems.slice(0, 6).map((item) => (
                    <Link to={item.href} key={item.id} className="ps-0">{item.name}</Link>
                  ))
                }
              </Nav>

              <Nav defaultActiveKey="/home" className="flex-column">
                {
                  navbarItems.slice(6, 12).map((item) => (
                    <Link to={item.href} key={item.id}>{item.name}</Link>
                  ))
                }
              </Nav>
            </div>
          </Col>
          <Col xs={12} md={4} className="mb-3">
            <h4 className="mb-4">{t('footer.follow')}</h4>
            <div className={styles.icons}>
              <a className="me-3" href="#">
                <FontAwesomeIcon icon={faGoogle} />
              </a>
              <a className="me-3" href="#">
                <FontAwesomeIcon icon={faLinkedinIn} />
              </a>
              <a className="me-3" href="#">
                <FontAwesomeIcon icon={faTwitter} />
              </a>
              <a className="me-3" href="#">
                <FontAwesomeIcon icon={faFacebookF} />
              </a>
            </div>
            <h4 className="my-4"> {t('footer.news')} </h4>
            <div className="d-flex align-items-center  flex-wrap  ">
              <input type="text" />
              <Button className={styles.btn} >{t('footer.subscripe')} </Button>
            </div>
          </Col>
        </Row>
      </Container>
      <p className={`${styles.copy} text-center pb-5 `}> {t('footer.copy')} </p>
    </div>
  );
}
